@import'../../common/styles/sass/variables.scss';
@import'../../common/styles/sass/mixins.scss';

.skill {
    min-height:250px ;
    width: 280px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: $textColor;
    font-family: $textFontFamily;
    margin: 30px 10px 0 5px;

    .icon {
        line-height: 2em;
        height: 60px;
        width: 60px;
        background-color: $bgSecondColor;
        border: 2px solid $primaryColor;
        font-size: 30px;
    }

    .description {
        @include text($textFontFamily, 14px, 1.8, 400);
    }
}